<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--    -->
    <el-card>
      <!--      -->
      <el-row>
        <el-col :span="7">
          <el-input placeholder="" v-model="queryInfo.title" @clear="getArticleList" clearable>
            <el-button slot="append" icon="el-icon-search" @click="getArticleList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--      -->
      <el-table :data="articleList" border stripe>
        <el-table-column type="index" align="center"></el-table-column>
        <el-table-column label="" prop="title" align="center"></el-table-column>
        <el-table-column label="" prop="nickname" align="center"></el-table-column>
        <el-table-column label="" prop="contentLue" align="center"></el-table-column>
        <el-table-column label="" prop="classification" align="center"></el-table-column>
        <el-table-column label="" prop="status" align="center">
          <template scope="scopes">
            <el-tag v-if="scopes.row.status === 1" size="mini" type="success"></el-tag>
            <el-tag v-else-if="scopes.row.status === 0" size="mini" type="info"></el-tag>
            <el-tag v-else size="mini" type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <!--            -->
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row)"></el-button>
            <!--            -->
            <el-button type="danger" icon="el-icon-delete" size="mini"
                       @click="removeArticleById(scope.row.id)"></el-button>
            <!--            -->
<!--            <el-tooltip effect="dark" content="" placement="top" :enterable="false">-->
<!--              <el-button type="warning" icon="el-icon-setting" size="mini" @click="audit(scope.row)"></el-button>-->
<!--            </el-tooltip>-->
          </template>
        </el-table-column>
      </el-table>

      <!--      -->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.current"
          :page-sizes="[2, 5, 10]"
          :page-size="queryInfo.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
    </el-card>

    <!---->
    <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%"
        @close="editDialogClosed"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="150px">
        <el-form-item label="" prop="title">
          <el-input v-model="editForm.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <!--          -->
          <quill-editor v-model="editForm.content"></quill-editor>
        </el-form-item>
        <el-form-item label="" prop="classificationIds">
          <el-select v-model="editForm.classificationIds" multiple placeholder="">
            <el-option
                v-for="item in catelist"
                :key="item.id"
                :label="item.name"
                :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editArticleInfo"> </el-button>
  </span>
    </el-dialog>

    <!---->
<!--    <el-dialog-->
<!--        title=""-->
<!--        :visible.sync="auditDialogVisible"-->
<!--        width="50%"-->
<!--        @close="auditDialogVisible = false"-->
<!--    >-->
<!--      <el-form :model="auditInfo" ref="auditInfoRef" label-width="100px">-->
<!--        <el-form-item label="">-->
<!--          <el-select v-model="auditInfo" placeholder="">-->
<!--            <el-option-->
<!--                v-for="item in rolesList"-->
<!--                :key="item.id"-->
<!--                :label="item.roleName"-->
<!--                :value="item.id">-->
<!--            </el-option>-->
<!--          </el-select>-->
<!--        </el-form-item>-->
<!--      </el-form>-->
<!--      <span slot="footer" class="dialog-footer">-->
<!--    <el-button @click="auditDialogVisible = false"> </el-button>-->
<!--    <el-button type="primary" @click="savedRoleInfo"> </el-button>-->
<!--  </span>-->
<!--    </el-dialog>-->
  </div>
</template>

<script>
export default {
  name: "Article",
  data() {
    return {
      queryInfo: {
        content: '',
        title: '',
        current: 1,
        size: 5
      },
      total: 0,
      articleList: [],
      classification: [],

      editDialogVisible: false,//
      editForm: {},//
      editFormRules: {
        title: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
        content: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
        classificationIds:[{
          required: true,
          message: '',
          trigger: 'blur'
        }]
      },//

      catelist:[],

      auditInfo:{},
      auditDialogVisible:false
    }
  },
  created() {
    this.getArticleList()
    this.getClassification()
  },
  methods: {
    getArticleList() {
      this.$http.get('article', {params: this.queryInfo}).then(res => {
        this.articleList = res.data.data.records
        var contentLue = []
        for (var i = 0; i < this.articleList.length; i++) {
          contentLue[i] = this.articleList[i].content.substr(0, 15)
          this.$set(this.articleList[i], 'contentLue', contentLue[i])
          var re1 = new RegExp("<.+?>", "g");//html"g"
          this.articleList[i].contentLue = this.articleList[i].contentLue
              .replace(re1, '')
              .replace(/(<br\s?\/?>)+/gi, '$1')        //
              .replace(/^<br\/>|<br>/g, '');        //;//
        }
        //
        for (var i = 0; i < this.articleList.length; i++) {
          this.classification[i] = ''
          for (var j = 0; j < this.articleList[i].classificationList.length; j++) {
            this.classification[i] = this.articleList[i].classificationList[j].name + ' ' + this.classification[i];
          }
          this.$set(this.articleList[i], 'classification', this.classification[i])
        }
        //
        for (var i = 0; i < this.articleList.length; i++) {
          this.$set(this.articleList[i], 'status', 1)
        }
        this.total = res.data.data.total
      }).catch(err => {
        console.log(err)
        this.$message.error('')
      })
    },
    // size 
    handleSizeChange(newSize) {
      // console.log(newSize)
      this.queryInfo.size = newSize
      this.getArticleList()
    },
    //  
    handleCurrentChange(newPage) {
      // console.log(newPage)
      this.queryInfo.current = newPage
      this.getArticleList()
    },
    //
    showEditDialog(row) {
      this.editForm = row
      this.editDialogVisible = true
    },
    //
    async removeArticleById(id) {
      const confirmResult = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonCText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      this.$http.delete('article?id=' + id).then(res => {
        this.$message.success('')
        this.getArticleList()
      }).catch(err => {
        console.log(err)
        this.$message.error('')
      })
    },
    getClassification(){
      this.$http.get('classification').then(res => {
        this.catelist = res.data.data
      }).catch(err => err)
    },
    //
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    //
    editArticleInfo () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        //
        const { data: res } = await this.$http.put('article', this.editForm)
        if (res.status !== 200) {
          return this.$message.error('')
        }
        //
        this.editDialogVisible = false
        //
        this.getArticleList()
        //
        this.$message.success('')
      })
    },
    //
    // audit(row){
    //   this.auditInfo = row
    //   this.auditDialogVisible = true
    // }
  }
}
</script>

